@import url('https://fonts.googleapis.com/css2?family=Potta+One&display=swap');

.label {
  height: 120px;
  width: 46px;
}

a{
  text-decoration: none;
  color: #000000;
}

.label .text-wrapper {
  color: #000000;
  font-family: "Potta One", Helvetica;
  font-size: 32px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: normal;
  position: fixed;
  text-shadow: 4px 4px 4px #00000040;
  transform: rotate(-90deg);
}

.label1 {
  height: 46px;
  width: 170px;
}

.label1 .text-wrapper1 {
  color: #000000;
  font-family: "Potta One", Helvetica;
  font-size: 32px;
  font-weight: 400;
  left: 0;
  letter-spacing: 0;
  line-height: normal;
  text-shadow: 4px 4px 4px #00000040;
}

.label2 {
  height: 46px;
  width: 200px;
}

.label2 .text-wrapper2 {
  color: #000000;
  font-family: "Potta One", Helvetica;
  font-size: 32px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: normal;
  position: fixed;
  text-shadow: 4px 4px 4px #00000040;
}

@media (max-width: 400px){

  

}

@media (max-width: 450px) {
  
  .label .text-wrapper {
    font-size: 20px;
    font-weight: 100;
  }

  .label1 .text-wrapper1 {
    font-size: 20px;
    font-weight: 100;
  }

  .label2 .text-wrapper2 {
    font-size: 20px;
    font-weight: 100;
  }

}

@media (max-width: 840px) {
  
  .label .text-wrapper {
    font-size: 20px;
    font-weight: 100;
  }

  .label1 .text-wrapper1 {
    font-size: 20px;
    font-weight: 100;
  }

  .label2 .text-wrapper2 {
    font-size: 20px;
    font-weight: 100;
  }

  @media (max-height: 820px) {

    .label2 .text-wrapper2{
      transform: rotate(-90deg);
    }
    
  }
}


